.row {
  background-size: cover;
  background-position: center;
  /*position: relative;*/
  border: 5px solid #778899;
  width: 80%;
  margin: 0 auto;
}

.hero {
  height: 240px;
  background-image: -webkit-gradient(
    linear,
    left top, right bottom,
    from(rgba(17, 3, 9, 0.6)), 
    to(rgba(233, 241, 247, 0.6))), 
  url(/images/coins.jpg);
  background-image: -webkit-linear-gradient(
    left top,
    rgba(17, 3, 9, 0.6), 
    rgba(233, 241, 247, 0.6)), 
  url(/images/coins.jpg);
  background-image: -o-linear-gradient(
    left top,
    rgba(17, 3, 9, 0.6), 
    rgba(233, 241, 247, 0.6)), 
  url(/images/coins.jpg);
  background-image: linear-gradient(
    to right bottom,
    rgba(17, 3, 9, 0.6), 
    rgba(233, 241, 247, 0.6)), 
  url(/images/coins.jpg);
 background-position: center;
  position: relative;
  border: 5px solid #778899;
  width: 80%;
  margin: 0 auto;
}

.heroText {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
}

.banner {
  display: block;
  font-size: 2vw;
  padding-top: 10px;
  font-family: Cinzel Decorative;
  font-weight: 300;
  color: black;
  text-shadow: 2px 5px 4px black;
}

.rules,
.message {
  display: block;
  font-size: 2rem;
}

.progress-bar {
  background-size: cover;
  background-position: center;
  position: relative;
  border: 2px solid #778899;
  width: 100%;
  
  border-radius: 35px;
}



